.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.header__options {
	height: 100%;
	margin: 0;
	padding: 0;
	display: flex;
	flex-direction: row;
	/* flex-wrap: wrap; */
	transition: ease 1s all;
}


.header__options li:first-child {
	font-family: 'Lobster', cursive;
}

.header__options li {
	list-style: none;
	height: 100%;
	width: calc(100% / 3);
	min-width: 20rem;
	background-color: #ecec;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: ease 1s all;
	font-weight: 800;
	font-size: 2rem;
	
}

.header {
	padding: 5px;
	position: relative;
	height: 16rem;
}
.card {
	width: 20.5rem;
	height: 15rem;
	padding: 0px;
	overflow: hidden;
	border-radius: 5%;
	background-color: #ececec;
	margin-bottom: .5rem;
	transition: all 2s 1s;
	border: 0.72rem solid #9473b0;
	position: relative;
}

.card img {
	position: relative;
	width: 30rem;
	left: -150px;
}
.card-overlay {
	background-color: #ececec;
	width: 20.5rem;
	height: 0rem;
}

.card:hover .card-overlay {
	position: absolute;
	background-color: rgba(214, 183, 226, 0.8);
	z-index: 3;
	width: 20.5rem;
	height: 15rem;
	top: 0px;
	left: 0px;
	font-size: 1.5em;
	font-weight: bold;
	align-items: center;
	display: flex;
	transition: height 1s;
}
.cards {
	width: 98%;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	margin: auto;
	transition: all 2s 1s;
}
